.g-stepcom {
  display: flex;
  flex-direction: column;
  padding: 10px 10px 56px 10px;
}
.g-stepcom .m-layout-wrap {
  margin-bottom: 10px;
}
.g-stepcom .m-img {
  width: 40%;
  height: 100px;
}
.g-stepcom .m-img > img {
  width: 100%;
  height: 100%;
}
.g-stepcom .m-name {
  font-size: 16px;
  padding: 5px;
  font-weight: 600;
}
.g-stepcom .m-intr {
  padding: 0 5px;
}
.g-stepcom .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-stepcom .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-stepcom .m-area {
  flex: 1 1;
  padding: 10px;
  align-items: flex-end;
  display: flex;
  justify-content: flex-end;
}
.g-stepcom .m-area > span {
  background: #0066aa;
  padding: 2px 5px;
  color: #fff;
  font-size: 12px;
}
.g-stepcom .shadow {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
}
.g-stepcom .m-brief {
  display: flex;
}
.g-stepcom .m-detail {
  padding: 16px;
}
.g-stepcom .m-detail .m-name {
  text-align: center;
  margin-bottom: 16px;
}
.g-stepcom .m-detail .m-img {
  width: 100%;
  height: auto;
  margin: 0 auto;
}
