.g-stepexp {
  display: flex;
  flex-direction: column;
  padding: 10px 10px 56px 10px;
}
.g-stepexp .m-ele-item {
  display: flex;
  margin: 0 0 10px 0;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
}
.g-stepexp .m-img {
  width: 40%;
  height: 100px;
}
.g-stepexp .m-img > img {
  width: 100%;
  height: 100%;
}
.g-stepexp .m-name {
  font-size: 14px;
  padding: 5px 10px;
}
.g-stepexp .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-stepexp .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-stepexp .m-area,
.g-stepexp .m-detail {
  flex: 1 1;
  padding: 0 10px;
  display: flex;
}
.g-stepexp .m-detail {
  justify-content: flex-end;
}
.g-stepexp .m-area > span {
  color: #cc0000;
  font-size: 12px;
}
.g-stepexp .m-btn {
  background: #eee;
  color: #666;
  height: 30px;
  padding: 3px 5px;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
